body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  background-color: #f0f0f0;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1 {
  font-size: 4.4rem;
  font-family: "Poppins", sans-serif;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  color: #000;
  font-size: 1.6rem;
  line-height: 1.8;
}

.login {
  width: 100%;
}
@media (min-width: 768px) {
  .login__grid {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
    height: 100vh;
  }
}
.login__contenido, .login__formulario {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem;
}
.login__contenido {
  background-color: #002E5F;
  color: #FFFFFF;
}
.login__texto {
  color: #FFFFFF;
  font-weight: 900;
  display: none;
}
@media (min-width: 768px) {
  .login__texto {
    display: block;
  }
}
.login__lista {
  display: none;
}
@media (min-width: 768px) {
  .login__lista {
    display: block;
    list-style-type: "🚀 ";
  }
}
.login__lista-item {
  margin-bottom: 1rem;
  font-weight: 300;
}
.login__lista-item:last-of-type {
  margin-bottom: 0;
}
.login__heading--azul {
  color: #002E5F;
}

.formulario {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  width: 90%;
  margin-top: 2rem;
  color: #002E5F;
}
@media (min-width: 768px) {
  .formulario {
    width: 80%;
  }
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.formulario__campo--inline {
  flex-direction: row;
}
.formulario__label, .formulario__campo {
  font-size: 1.6rem;
}
.formulario__input {
  outline: none;
  border: none;
  padding: 0.8rem;
  border-radius: 1rem;
  color: #002E5F;
}
.formulario__input--error {
  border: 1px solid red;
}
.formulario__boton {
  background-color: #002E5F;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  padding: 1rem 3rem;
  font-size: 1.6rem;
  border-radius: 1rem;
  margin-top: 0;
}
@media (min-width: 768px) {
  .formulario__boton {
    width: auto;
  }
}
.formulario__boton:hover {
  background-color: #002750;
  cursor: pointer;
}
.formulario__boton--disabled {
  opacity: 50%;
}
.formulario__mensaje {
  font-size: 1.2rem;
  display: block;
}
.formulario__mensaje--error {
  color: red;
}
.formulario__botones {
  display: flex;
  gap: 2rem;
  justify-content: space-between;
}
.formulario__listado {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}
.formulario__tag {
  background-color: #002E5F;
  color: #FFFFFF;
  font-size: 1.4rem;
  padding: 0.5rem 1rem;
  border-radius: 10rem;
  list-style: none;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.formulario__tag--eliminar {
  cursor: pointer;
  font-size: 1rem;
}

.preguntas__grid {
  display: grid;
  gap: 0;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .preguntas__grid {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
    height: 100vh;
  }
}
.preguntas__contenido, .preguntas__formulario {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem;
}
.preguntas__contenido {
  background-color: #002E5F;
  color: #FFFFFF;
  grid-row: 1/2;
}
@media (min-width: 768px) {
  .preguntas__contenido {
    grid-row: unset;
  }
}
.preguntas__texto {
  color: #FFFFFF;
  font-weight: 400;
  display: none;
}
@media (min-width: 768px) {
  .preguntas__texto {
    display: block;
  }
}
.preguntas__heading--azul {
  font-size: 2rem;
  text-align: center;
  font-weight: 400;
  color: #002E5F;
}

.header {
  background-color: #002E5F;
  padding: 1rem 5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .header {
    flex-direction: row;
  }
}
.header__heading {
  margin: 0;
  color: #bf942b;
}
.header__contenido {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.header__usuario {
  color: #FFFFFF;
  margin: 0;
}
.header__icono {
  color: #FFFFFF;
  transition: all 0.3s ease;
}
.header__icono:hover {
  color: #e6e6e6;
}

.correos {
  position: relative;
  height: 100vh;
  overflow: scroll;
  padding-bottom: 6.8rem;
}

.correo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #FFFFFF;
  transition: all 0.3s ease;
  padding: 1rem;
}
.correo:hover {
  background-color: #c5e1ff;
  cursor: pointer;
}
.correo--activo {
  background-color: #5facff;
}
.correo__propietario-iniciales {
  width: 15%;
}
.correo__propietario-iniciales span {
  display: inline-block;
  width: 5rem;
  height: 5rem;
  background-color: #FFFFFF;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  color: #002E5F;
}
.correo__informacion {
  display: flex;
  flex-direction: column;
  width: 85%;
}
.correo__propietario-nombre, .correo__premensaje, .correo__hora {
  margin: 0;
}
.correo__premensaje {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  font-size: 1.2rem;
  -webkit-line-clamp: 1;
  overflow: hidden;
  width: 80%;
}
.correo__hora {
  font-size: 1.2rem;
}
.correo__contenido {
  display: flex;
}

@media (min-width: 768px) {
  .dashboard__grid {
    display: grid;
    grid-template-columns: 2fr 4fr;
  }
}